<template>
  <nav class="navbar is-right" :class="{'is-fixed-top': isActive}" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <a class="navbar-item">
        <img src="data:image/png;base64,"
          width="auto" height="auto">
        <span class="nav-title"><strong>AscoMusic</strong></span>
      </a>
      <a class="navbar-burger burger" role="button" :class="{'is-active': isActive}" @click="toggle()" aria-label="menu"
        aria-expanded="false" data-target="navbarBasicExample">
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>
    <div class="navbar-menu" id="navbarBasicExample" :class="{'is-active': isActive}">
      <div class="navbar-start"></div>
      <div class="navbar-end">
        <router-link tag="a" to="/" class="navbar-item">Home</router-link>
        <a class="navbar-item">About</a>
         <router-link tag="a" v-show="$route.name != 'Login'" to="/profile" class="navbar-item">Profile</router-link>
        <div class="navbar-item has-dropdown is-hoverable">
          <a class="navbar-link">Language</a>
          <div class="navbar-dropdown is-active">
            <a class="navbar-item">
              <span class="icon">
                <flag iso="mx" />
              </span>
              <span>ES</span>
            </a>
            <a class="navbar-item">
              <span class="icon">
                <flag iso="us"/>
              </span>
              <span>EN</span>
            </a>
          </div>
        </div>
        <div class="navbar-item">
          <div class="buttons">
            <a class="button is-danger" v-if="$route.name != 'Login'" @click="logout">Logout</a>
            <router-link tag="a" v-else to="/login" class="button is-light">Log in</router-link>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      isActive: false
    }
  },
  methods: {
    logout () {
      localStorage.clear()
      this.$store.commit('track/setTrack', {})
      this.$router.push('/login')
    },
    toggle () {
      this.isActive = !this.isActive
    }
  }
}
</script>

<style>
.nav-title {
  padding-left: 10%;
}
</style>



